body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.searchapp {
  background-color: #2185D0;
  margin: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.simple-item .simple-item-extra {
  font-size: 80%;
}
.listing-view-item.header {
  cursor: pointer;
}
.listing-view-details .listing-view-details-section {
  padding: 1em 0em;
}
.listing-view-details .listing-view-details-section .details-field {
  display: flex;
}
.listing-view-details .listing-view-details-section .details-field .details-field-label {
  width: 20%;
  font-weight: bold;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: 0.3em 1em 0.3em 0.3em;
}
.listing-view-details .listing-view-details-section .details-field .details-field-value {
  width: 80%;
  padding: 0.3em 0.3em 0.3em 1em;
}
.body-content,
.body-footer {
  background-color: white;
}
.ui.four.cards > .card.card-item {
  font-size: 0.8em;
}
.ui.four.cards > .card.card-item.hovered a.image {
  height: 80px;
}
.ui.four.cards > .card.card-item.hovered .content {
  max-height: 13em;
}
.ui.four.cards > .card.card-item.hovered .content .description {
  max-height: 7em;
}
.ui.four.cards > .card.card-item.hovered .controls {
  display: block;
}
.ui.four.cards > .card.card-item .controls {
  display: none;
}
.ui.four.cards > .card.card-item a.image {
  height: 180px;
  background-size: cover;
  background-position: center;
}
.ui.four.cards > .card.card-item a.image img {
  display: none;
}
.ui.four.cards > .card.card-item .label.meta-type {
  position: absolute;
  margin-top: 50px;
  z-index: 2;
  border-radius: 0px !important;
  border-top-right-radius: 5px !important;
  border-bottom-right-radius: 5px !important;
  background-color: #009590;
  color: white;
}
.ui.four.cards > .card.card-item .header {
  padding-top: 0.8em;
}
.ui.four.cards > .card.card-item .content {
  overflow: hidden;
  max-height: 8em;
}
.ui.four.cards > .card.card-item .content .description {
  max-height: 4em;
  overflow: hidden;
}
.ui.four.cards > .card.card-item .content .meta {
  white-space: nowrap;
  overflow: hidden;
}
.view-selector {
  padding-bottom: 1em;
}
.search-body-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search-body-footer .sui-paging-info {
  flex-grow: 1;
}
.facets {
  margin-top: 1em;
}
.facets .accordion.ui {
  border-bottom: 1px solid #ccc;
  padding: 0.4em 0em;
}
.facets .accordion.ui:hover {
  background-color: #eee;
}
.facets .accordion.ui .content {
  background-color: white;
  padding-bottom: 0.5em !important;
}
.facets .accordion.ui .title {
  white-space: nowrap;
}
.facets .accordion.ui .sui-facet__title {
  display: none;
