}
.ui.horizontal.divider:before {
  background-position: right 1em top 50%;
}
.ui.horizontal.divider:after {
  background-position: left 1em top 50%;
}
/*--------------
    Vertical
---------------*/
.ui.vertical.divider {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  margin: 0rem;
  padding: 0em;
  width: auto;
  height: 50%;
  line-height: 0em;
  text-align: center;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
