.card-item .card-icon,
.horizontal-card-item .card-icon {
  width: 30px;
  height: 30px;
  background: #fff no-repeat center;
  background-size: 100%;
  border-radius: 50%;
}
a.ui.mini.circular.compact.icon.right.floated.button.metaButton {
  padding: 3px;
}
.horizontal-card-thumbnail {
  display: block;
  float: left;
  margin-right: 0.6em;
  height: 100%;
  min-height: 10em;
  background-size: cover;
  background-position: center;
}
.horizontal-card-thumbnail img {
  width: 100%;
  visibility: hidden;
}
.horizontal-card-item .content.details .header a {
  font-size: 18px;
  font-weight: 700;
  color: #004b87;
}
.horizontal-card-item .content.details .header a:hover {
  color: #8e004d;
}
/* End Config */
/*******************************
           UI Tabs
*******************************/
.ui.tab {
  display: none;
}
/*******************************
             States
*******************************/
/*--------------------
       Active
---------------------*/
.ui.tab.active,
.ui.tab.open {
  display: block;
}
/*--------------------
       Loading
---------------------*/
.ui.tab.loading {
  position: relative;
  overflow: hidden;
  display: block;
  min-height: 250px;
}
.ui.tab.loading * {
  position: relative !important;
  left: -10000px !important;
}
.ui.tab.loading:before,
.ui.tab.loading.segment:before {
  position: absolute;
  content: '';
  top: 100px;
  left: 50%;
  margin: -1.25em 0em 0em -1.25em;
  width: 2.5em;
  height: 2.5em;
  border-radius: 500rem;
  border: 0.2em solid rgba(0, 0, 0, 0.1);
}
.ui.tab.loading:after,
.ui.tab.loading.segment:after {
  position: absolute;
  content: '';
  top: 100px;
  left: 50%;
  margin: -1.25em 0em 0em -1.25em;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation: button-spin 0.6s linear;
          animation: button-spin 0.6s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  border-radius: 500rem;
  border-color: #767676 transparent transparent;
  border-style: solid;
  border-width: 0.2em;
  box-shadow: 0px 0px 0px 1px transparent;
}


